* {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
body {
  margin: 0;
  padding: 0;
}
a {
  color: inherit;
}
.header-menu-fon {
  background-color: #1B2D37;
}
.header-menu-fon h1,
.header-menu-fon p,
.header-menu-fon li {
  color: #FFF;
}
.menu__button {
  background-color: rgba(255, 255, 255, 0);
  color: #FFF;
  border: 1px solid #FFF;
}
.menu__button:hover {
  color: #00B2FF;
  border: 1px solid #00B2FF;
}
.header__button {
  border: 1px solid #00B2FF;
  background-color: #00B2FF;
  color: #FFF;
}
.header__button:hover {
  background-color: #FFF;
  color: #00B2FF;
}
.header__item a > p {
  color: rgba(255, 255, 255, 0.5);
}
.advantages {
  background-color: #FFF;
}
.advantages__item {
  border: 1px solid #EBEBEB;
  border-left: 2px solid #00B2FF;
}
.advantages__item h4 {
  color: #00B2FF;
}
.advantages__item p {
  color: #666666;
}
.advantages__item:hover {
  background-color: #00B2FF;
}
.advantages__item:hover svg path {
  fill: #FFF;
}
.advantages__item:hover h4 {
  color: #FFF;
}
.advantages__item:hover p {
  color: #FFF;
}
.about-company {
  background-color: #FFF;
}
.about-company_text h3,
.about-company_text p {
  color: #666666;
}
.about-company__button {
  border: 1px solid #00B2FF;
  background-color: #FFF;
  color: #00B2FF;
}
.about-company__button:hover {
  background-color: #00B2FF;
  color: #FFF;
}
.about-company__button:hover {
  border: 1px solid #FFF;
}
.services {
  background-color: #1B2D37;
}
.services h2,
.services p {
  color: #FFF;
}
.services__production,
.services__montage,
.services__complect {
  background-blend-mode: overlay;
  background-color: rgba(27, 45, 55, 0.7);
}
.services__button {
  background-color: #00B2FF;
  color: #FFF;
}
.services__button:hover {
  background-color: #FFF;
  color: #00B2FF;
}
.services__button:hover {
  border: 1px solid #00B2FF;
}
.consult {
  background-color: #00B2FF;
  color: #FFF;
}
.consult input,
.consult textarea {
  background-color: rgba(255, 255, 255, 0.1);
  color: #FFF;
  border: 1px solid #00B2FF;
}
.consult input:focus,
.consult textarea:focus {
  border: 1px solid #FFF;
}
.consult__button {
  background-color: #00B2FF;
  color: #FFF;
  border: 1px solid #FFF;
}
.consult__button:hover {
  background-color: #FFF;
  color: #00B2FF;
}
.consult__button:hover {
  border: 1px solid #00B2FF;
}
.footer {
  background-color: #FFF;
}
.footer h4 {
  color: #1B2D37;
}
.footer p {
  color: rgba(27, 45, 55, 0.5);
}
.footer__bottom-container {
  border-top: 1px solid rgba(98, 105, 98, 0.2);
}
@font-face {
  font-family: 'PT Mono';
  src: url('../fonts/PTMono-Regular.woff2') format('woff2'), url('../fonts/PTMono-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Roboto Condensed';
  src: url('../fonts/RobotoCondensed-Regular.woff2') format('woff2'), url('../fonts/RobotoCondensed-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Roboto Condensed';
  src: url('../fonts/RobotoCondensed-Bold.woff2') format('woff2'), url('../fonts/RobotoCondensed-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
}
p,
li,
span {
  font-family: 'PT Mono', monospace;
}
h1,
h2,
h3,
h4,
h5 {
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: normal;
}
button {
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: bold;
  text-transform: uppercase;
}
a {
  text-decoration: none;
}
.header h1 {
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
}
@media (min-width: 768px) and (max-width: 1023px) {
  .header h1 {
    font-size: 60px;
    line-height: 70px;
  }
}
@media (min-width: 320px) and (max-width: 767px) {
  .header h1 {
    font-size: 40px;
    line-height: 47px;
  }
}
.header p {
  font-size: 22px;
  line-height: 34px;
}
@media (min-width: 768px) and (max-width: 1023px) {
  .header p {
    font-size: 18px;
    line-height: 28px;
  }
}
@media (min-width: 320px) and (max-width: 767px) {
  .header p {
    font-size: 16px;
    line-height: 25px;
  }
}
.header__button {
  font-size: 16px;
  line-height: 19px;
}
.header__item a > p {
  font-size: 14px;
  line-height: 16px;
}
.advantages__item h4 {
  font-size: 24px;
  line-height: 34px;
}
@media (min-width: 320px) and (max-width: 767px) {
  .advantages__item h4 {
    font-size: 20px;
    line-height: 28px;
  }
}
.advantages__item p {
  font-size: 16px;
  line-height: 22px;
}
.about-company_text h3 {
  font-size: 34px;
  line-height: 51px;
}
@media (min-width: 768px) and (max-width: 1023px) {
  .about-company_text h3 {
    font-size: 32px;
    line-height: 45px;
  }
}
@media (min-width: 320px) and (max-width: 767px) {
  .about-company_text h3 {
    font-size: 28px;
    line-height: 39px;
  }
}
.about-company_text p {
  font-size: 16px;
  line-height: 22px;
}
.about-company__button {
  font-size: 14px;
  line-height: 16px;
}
.services h2 {
  font-size: 36px;
  text-align: left;
}
.services__images p {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 20px;
}
.services__button {
  font-size: 24px;
  font-weight: normal;
}
.consult h3 {
  font-size: 36px;
  line-height: 51px;
}
.consult p {
  font-size: 16px;
  line-height: 22px;
}
.footer__top-container {
  font-size: 15px;
}
.footer__top-container h4 {
  line-height: 16px;
  text-transform: uppercase;
}
.footer__top-container p {
  line-height: 25px;
}
.footer__bottom-container {
  font-size: 13px;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;
}
.header-menu-fon {
  background-image: url("../img/ocean.svg");
  padding-top: 32px;
}
.header-menu-fon .menu {
  margin-left: 4%;
  margin-right: 8%;
}
.header-menu-fon .menu ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
}
.header-menu-fon .menu ul li {
  list-style-type: none;
  vertical-align: middle;
}
.header-menu-fon .menu ul li span ~ svg {
  max-width: 19px;
}
@media (max-width: 1023px) {
  .header-menu-fon .menu ul li span ~ svg {
    max-width: 25px;
  }
}
@media (max-width: 1023px) {
  .header-menu-fon .menu ul li span {
    display: none;
  }
}
@media (max-width: 768px) {
  .menu img[src*="white"] {
    display: none;
  }
}
@media (min-width: 767px) {
  .menu img[src*="mobile"] {
    display: none;
  }
}
.menu__button {
  border-radius: 5px;
  margin-top: 20px;
  padding: 20px 50px;
}
.menu__button:hover {
  cursor: pointer;
}
@media (max-width: 1023px) {
  .menu__button {
    display: none;
  }
}
.header {
  padding: 14% 9% 5% 6%;
  display: flex;
  justify-content: center;
}
@media (min-width: 320px) and (max-width: 767px) {
  .header {
    flex-direction: column-reverse;
  }
}
.header p {
  margin-left: 90px;
}
.header__button {
  width: 407px;
  height: 55px;
  padding: 20px 40px;
  border-radius: 5px;
  margin-top: 30px;
  margin-left: 90px;
}
@media (min-width: 768px) and (max-width: 1023px) {
  .header__button {
    width: 379px;
    height: 55px;
  }
}
@media (min-width: 320px) and (max-width: 767px) {
  .header__button {
    width: 287px;
    height: 55px;
  }
}
.header__button:hover {
  cursor: pointer;
}
@media (min-width: 320px) and (max-width: 767px) {
  .header__button_desctop {
    display: none;
  }
}
.header__button_mobile {
  display: none;
}
@media (min-width: 320px) and (max-width: 767px) {
  .header__button_mobile {
    display: inline;
  }
}
/*прямоугольник на линии с абзацем?*/
.header__item {
  /*мигающий скролл вниз?*/
}
.header__item a > p {
  transform: rotate(-90deg);
  margin-top: 100px;
  margin-left: -40px;
  width: 93px;
  height: 16px;
}
@media (max-width: 1023px) {
  .header__item a > p {
    display: none;
  }
}
.header__item img {
  width: 100%;
}
@media (min-width: 320px) and (max-width: 767px) {
  .header__item {
    width: 80%;
  }
}
.advantages-about-company-fon {
  background-image: url("../img/advantages-fon.svg");
  background-position: strech;
  padding: 9% 11% 13%;
  background-size: cover;
}
@media (min-width: 768px) and (max-width: 1023px) {
  .advantages-about-company-fon {
    padding: 7% 3% 10%;
  }
}
@media (min-width: 320px) and (max-width: 767px) {
  .advantages-about-company-fon {
    padding: 11% 5% 19%;
  }
}
.advantages {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
@media (min-width: 320px) and (max-width: 767px) {
  .advantages {
    flex-direction: column;
    align-items: center;
  }
}
.advantages img {
  max-width: 36px;
}
@media (max-width: 1023px) {
  .advantages img {
    max-width: 32px;
  }
}
.advantages__item {
  width: 47%;
  padding: 50px;
  margin: 10px;
}
@media (min-width: 768px) and (max-width: 1023px) {
  .advantages__item {
    width: 46%;
    padding: 30px;
  }
}
@media (min-width: 320px) and (max-width: 767px) {
  .advantages__item {
    width: 100%;
    padding: 20px;
  }
}
.advantages__item:hover {
  cursor: pointer;
}
/*отступы скорректировать*/
.about-company {
  margin-top: 90px;
  display: flex;
}
@media (min-width: 320px) and (max-width: 767px) {
  .about-company img {
    display: none;
  }
}
.about-company__button {
  border-radius: 5px;
  margin-top: 20px;
  padding: 20px 50px;
}
.about-company__button:hover {
  cursor: pointer;
}
.services {
  padding: 5% 11% 13%;
}
.services__images {
  margin-top: 40px;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1023px) {
  .services__images {
    flex-direction: column;
    align-items: center;
  }
}
.services__production {
  background-image: url("../img/production.png");
}
.services__montage {
  background-image: url("../img/montage.png");
}
.services__complect {
  background-image: url("../img/complect.png");
}
.services__production,
.services__montage,
.services__complect {
  width: 30%;
  height: 620px;
  display: flex;
  justify-content: flex-end;
}
@media (min-width: 768px) and (max-width: 1023px) {
  .services__production,
  .services__montage,
  .services__complect {
    width: 100%;
    height: 256px;
    margin-bottom: 23px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}
@media (min-width: 320px) and (max-width: 767px) {
  .services__production,
  .services__montage,
  .services__complect {
    width: 100%;
    height: 256px;
    margin-bottom: 23px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    margin-bottom: 15px;
  }
}
.services__images p {
  margin-top: 50px;
  margin-left: 50px;
}
.services__button {
  width: 63px;
  height: 63px;
  border: none;
  align-self: flex-end;
  flex-shrink: 0;
}
.consult {
  height: 670px;
  background-image: url("../img/consult-fon.svg");
  background-position: strech;
  padding: 5% 11% 13%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.consult img {
  width: 511px;
  height: 288px;
}
@media (max-width: 1023px) {
  .consult img {
    display: none;
  }
}
.consult__form p {
  margin-bottom: 49px;
}
.consult__form input[type="text"] {
  margin-right: 31px;
}
.consult__form input[type^="te"] {
  width: 270px;
  height: 50px;
}
.consult__form input[type="checkbox"] {
  width: auto;
}
.consult__form textarea {
  width: 570px;
  height: 154px;
  margin-top: 40px;
  margin-bottom: 43px;
}
.consult__button {
  width: 270px;
  height: 45px;
  border-radius: 5px;
  padding: 20px 50px;
}
.consult__button:hover {
  cursor: pointer;
}
.footer__top-container {
  height: 320px;
  max-width: 1920px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.footer__logo img {
  height: 52px;
  width: 216px;
}
.footer__socials img {
  height: 26px;
  width: 26px;
  margin-top: 80px;
  margin-right: 15px;
}
.footer__bottom-container {
  display: flex;
  justify-content: space-around;
}
